.visually-hidden {
  visibility: hidden;
  padding: 0;
  margin: 0;
  font-size: 0;
  line-height: 0;
}
body {
  width: 100%;
  margin: 0;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-display: swap;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
html,
body {
  box-sizing: border-box;
  overflow-x: hidden;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 84px;
  background: #f5f5f9;
}
.header__wrapper {
  position: relative;
  width: 50%;
}
.header__logo {
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 55px;
  height: 55px;
  left: 376px;
  top: 15px;
  cursor: pointer;
  text-decoration-line: none;
}
.header__logo p {
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.13em;
  color: #48494d;
  text-transform: uppercase;
  padding-left: 20px;
}
.main-nav {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 84px;
  padding-left: 65px;
}
.main-nav__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 84px;
}
.site-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  padding: 0;
  margin: 0;
}
.site-list__item {
  display: flex;
  align-items: center;
  list-style: none;
}
.site-list__item a {
  display: block;
  padding: 0;
  margin: 0;
  margin-right: 68px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  text-decoration: none;
  color: #48494d;
}
.site-list__item a:hover {
  opacity: 1;
}
.site-list__item a:active {
  opacity: 0.4;
}
.footer-menu {
  display: flex;
  flex-direction: row;
  max-width: 100%;
  height: 82px;
  margin-top: 140px;
  align-items: center;
  background: #f5f5f9;
}
.footer-menu__list {
  display: flex;
  flex-direction: row;
  max-width: 90%;
  justify-content: center;
  margin: 0;
  margin: 0 auto;
  padding: 0;
}
.footer-menu__item {
  list-style: none;
  text-decoration: none;
}
.footer-menu__item a {
  display: block;
  padding: 0;
  margin: 0;
  margin-right: 33px;
  margin-left: 33px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  text-decoration: none;
  color: #48494d;
}
.footer-menu__item a:hover {
  opacity: 1;
}
.footer-menu__item a:active {
  opacity: 0.4;
}
.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  padding-top: 90px;
  background: #ffffff;
}
.card__wrapper {
  display: flex;
  flex-direction: row;
}
.card__wrapper--auto {
  width: 1170px;
  margin: 0 auto;
  padding: 0;
}
.card__list {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  padding-top: 19px;
}
.card__img {
  display: flex;
  flex-direction: row;
  width: 50%;
  justify-content: flex-end;
}
.card__description {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: left;
  padding-left: 54px;
}
.card__item {
  display: flex;
  flex-direction: column;
  width: 76px;
  align-items: center;
  justify-content: center;
  list-style: none;
}
.card__description h3,
p {
  display: flex;
  padding: 0;
  margin: 0;
}
.card__description h2 {
  width: 100%;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 41px;
  padding: 0;
  margin: 0;
  margin-top: -10px;
  color: #48494d;
}
.card__description h3 {
  width: 189px;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 35px;
  line-height: 48px;
  color: #48494d;
  letter-spacing: -1.9px;
}
.card__description p {
  position: relative;
  width: 123px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: rgba(72, 73, 77, 0.5);
}
.card__description p::before {
  position: absolute;
  content: "";
  width: 87%;
  height: 1px;
  background: #a3a4a6;
  top: 50%;
  left: 0;
  transform: rotate(-7deg);
}
.card__item p {
  max-width: 76px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  padding: 0;
  margin: 0;
  text-align: center;
  justify-content: center;
}
.card__item p::before {
  display: none;
}
.card__price {
  display: flex;
  width: 342px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 37px 0 37px 0;
}
.card__btn {
  display: flex;
  flex-direction: row;
  width: 196px;
  height: 52px;
  padding: 16.5px 20px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}
.card__btn--request {
  margin-bottom: 16px;
  background: #d12136;
  color: #ffffff;
  border: none;
}
.card__btn--credit {
  width: 222px;
  background: #ffffff;
  border: 1px solid #d12136;
  color: #d12136;
}
.card__img--slide2,
.card__img--slide3 {
  display: none;
}
.card__wrapper--slider {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 50%;
  padding-top: 39px;
}
.card__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 600px;
}
.card-slider__btn path {
  stroke: inherit;
  stroke-width: inherit;
  fill: inherit;
}
.card-slider__btn {
  fill: white;
  cursor: pointer;
}
.card-slider__btn--left {
  stroke: #d7d9df;
}
.card-slider__btn--right {
  stroke: #48494d;
}
.card__icon--motor,
.card__icon--transmission {
  position: relative;
  fill: white;
}
.interaction {
  width: 100%;
  padding-top: 90px;
}
.interaction__container {
  position: relative;
  display: flex;
  width: 1170px;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 auto;
  padding: 0;
}
.interaction__tab {
  display: flex;
  flex-direction: row;
  width: 585px;
}
.interaction__tab-links {
  width: 195px;
  height: 50px;
  padding: 0;
  margin: 0;
  border: 1px solid #d7d9df;
  box-sizing: border-box;
  transition: 0.3s;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  background-color: #ffffff;
  color: #48494d;
  outline: #48494d;
}
.interaction__table {
  width: 585px;
  border-collapse: collapse;
}
.interaction__table tr {
  height: 50px;
  border-bottom: 1px solid #d7d9df;
  box-sizing: border-box;
}
.interaction__table th {
  height: 50px;
  text-align: left;
  vertical-align: middle;
}
.interaction__table td {
  text-align: right;
  padding-right: 20px;
}
.interaction__wrapper--comment {
  display: none;
  flex-direction: column;
  width: 1170px;
  margin: 0 auto;
  padding: 0;
}
.interaction__wrapper--contact {
  display: none;
  flex-direction: row;
  width: 779px;
  height: 272px;
  padding-top: 30px;
}
.interaction-contact__container {
  display: flex;
  flex-direction: column;
}
.interaction__comment h2 {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #48494d;
}
.interaction__comment h3 {
  position: relative;
  margin-top: 12px;
  padding-left: 15px;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
}
.interaction__comment h3:first-of-type::before {
  position: absolute;
  content: "+";
  top: 0;
  left: 0;
  color: #ff0000;
}
.interaction__comment h3:nth-of-type(2)::before {
  position: absolute;
  content: "-";
  top: 0;
  left: 0;
  color: #ff0000;
}
.interaction__comment h3:nth-of-type(3) {
  padding-left: 0;
}
.interaction__comment p {
  display: flex;
  justify-content: space-between;
}
.interaction__comment {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.2);
}
.interaction__advice {
  display: flex;
  flex-direction: row;
  padding: 20px 0 30px 0;
}
.interaction__advice p {
  padding-left: 20px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 20px;
  color: #d12136;
}
.interaction__star {
  padding-right: 2px;
}
.interaction__star--red {
  fill: #d12136;
}
.interaction__star--grey {
  fill: #bdbec2;
}
.interaction__answer {
  display: flex;
  flex-direction: row;
  width: 203px;
  justify-content: space-between;
  padding-bottom: 21px;
}
.interaction__button {
  width: 73px;
  height: 22px;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #d12136;
  background-color: white;
  text-align: center;
  border: none;
}
.interaction__answer p {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
  opacity: 0.4;
}
.interaction__comment--first {
  position: relative;
}
.interaction__btnfeedback {
  position: absolute;
  width: 192px;
  height: 52px;
  right: 0;
  top: 0;
  border: 1px solid #d12136;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #d12136;
  background-color: white;
}
.interaction-contact__container--adress {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 348px;
  height: 271px;
  padding-right: 74px;
}
.interaction-contact__container--adress h3 {
  padding: 0;
  margin: 0;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #1a212f;
}
.interaction-contact__container--adress p {
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
}
.interaction-contact__container--adress a {
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  text-decoration: none;
  color: #000000;
}
.interaction-contact__container--map {
  position: relative;
  height: 271px;
}
.interaction-box__map {
  position: absolute;
  top: 0;
  left: 0;
  width: 431px;
  height: 271px;
  z-index: 3;
}
.popup--active {
  position: absolute;
  top: 665px;
  right: 50%;
  display: flex;
  width: 698px;
  height: 415px;
  transform: translateX(50%);
  background-color: #ffffff;
  z-index: 3;
}
.popup-background {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.popup-background--disabled {
  display: none;
}
.popup {
  display: flex;
  flex-direction: column;
  background-color: #f5f5f9;
}
.popup__close {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
}
.popup__close:hover {
  opacity: 1;
}
.popup__close::before,
.popup__close::after {
  position: absolute;
  left: 15px;
  content: " ";
  height: 17px;
  width: 2px;
  background-color: #333333;
}
.popup__close::before {
  transform: rotate(45deg);
}
.popup__close::after {
  transform: rotate(-45deg);
}
.popup h2 {
  width: 203px;
  margin: 0 auto;
  padding: 0;
  padding-top: 30px;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 34px;
  color: #48494d;
}
.popup__form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-top: 24px;
}
.popup__form input {
  border: 1px solid rgba(72, 73, 77, 0.2);
}
.popup__form input:focus {
  outline: 1px solid rgba(72, 73, 77, 0.7);
}
.popup__form textarea:focus {
  outline: 1px solid rgba(72, 73, 77, 0.7);
}
.popup__form fieldset {
  border: none;
}
.popup__element--name {
  display: flex;
  flex-direction: column;
  width: 49%;
  height: 230px;
  margin: 0 auto;
  padding: 0;
  padding: 0 0 0 35px;
}
.popup__label--name {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #ff0000;
  padding-bottom: 3px;
}
.popup__element--name input {
  width: 300px;
  height: 50px;
  margin-bottom: 16px;
  padding-left: 25px;
}
input[name="name"] {
  border-color: #ff0000;
}
.popup__wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 25px 0 30px 0;
}
.popup__element--rate {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 50%;
  height: 65px;
}
textarea[name="comment"] {
  width: 300px;
  height: 118px;
  border: 1px solid rgba(72, 73, 77, 0.2);
  padding: 20px 0 0 25px;
}
.popup__span {
  position: relative;
}
.popup__span--comment::before {
  position: absolute;
  top: 15px;
  left: 15px;
  content: "*";
  color: red;
}
.popup__span--name::before {
  position: absolute;
  top: 10px;
  left: 15px;
  content: "*";
  color: red;
}
.popup__element--rate p {
  width: 50%;
  font-family: "Open Sans-Bold", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
}
.popup__element--rate p:hover {
  color: #d12136;
}
.popup__rate {
  display: flex;
  flex-direction: row;
  width: 50%;
  border: none;
  padding: 0;
  margin: 0;
}
.popup__input {
  display: none;
}
.popup__label--nostar::before {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  cursor: pointer;
  content: url("../img/bigstar.svg");
}
.popup__label--nostar:hover {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  cursor: pointer;
  content: url("../img/bigstarred.svg");
}
input[type="checkbox"]:checked + .popup__label--star::before {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  content: url("../img/bigstarred.svg");
}
.popup--disabled {
  display: none;
}
.popup__btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 190px;
  height: 52px;
  margin: 0 auto;
  padding: 0;
  margin-top: 10px;
  background: #d12136;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: #ffffff;
  border: none;
}
